@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@charset "ISO-8859-1";

*{
    box-sizing: border-box;
}
.body body{
	font-family: 'Roboto', sans-serif;
	font-weight: 300;
	background: #181818;
	color: #fff;
	overflow: hidden
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 300;
}

.wrapper
{
    display: flex;
    flex-direction: row;
}

.left
{
    display: flex;
    flex-direction: column;
    flex: 1 1;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: #181818;
}

.right
{
    flex: 1 1;
}

.signin
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    padding-bottom: 1rem;
}

.signin form
{
    width: 80%;
    padding-bottom: 3rem;
}

.signin .logo
{
    margin-bottom: 2vh;
}

.signin .logo img
{
    width: 200px;
}

.signin label
{
    font-size: 0.9rem;
    line-height: 2rem;
    font-weight: 500;
}

.signin .text-input
{
    margin-bottom: 1.3rem;
    width: 100%;
    border-radius: 2px;
    background: #181818;
    border: 1px solid #555;
    color: #8B0000;
    padding: 0.5rem 1rem;
    line-height: 1.3rem;
}

/* Media Query */
@media(min-width: 1200px)
{
    .left
    {
        flex: 4 1;
    }

    .right
    {
        flex: 6 1;
    }
}

/* Button */
.signin .primary-btn
{
    width: 100%;
}
.primary-btn
{
    padding: 0.7rem 1rem;
    height: 2.7rem;
    display: block;
    border: 0;
    border-radius: 2px;
    font-weight: 500;
    background: #8B0000;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
}

.primary-btn:hover
{
    background-color: #DC143C;
}

.secondary-btn
{
    padding: 0.7rem 1rem;
    height: 2.7rem;
    display: block;
    border: 1px solid #f4f4f4;
    border-radius: 2px;
    font-weight: 500;
    background: none;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
}

.secondary-btn:hover
{
        border-color: #DC143C;
        color: #8B0000;
}

.signin .secondary-btn, .or, .links
{
width: 60%;
}

.signin .links a
{
    display: block;
    color: #fff;
    text-decoration: none;
    margin-bottom: 1rem;
    text-align: center;
    font-size: 00.9rem;
}

.signin .or
{
display: flex;
flex-direction: row;
margin-bottom: 1.2rem;
align-items: center;
}

.signin .or .bar
{
flex: auto;
border: none;
height: 1px;
background: #aaa;
}

.signin .or span
{
 color: #ccc;
 padding: 0 0.8rem;
}

.main-footer
{
    color: #ccc;
    text-align: center;
    font-size: 0.8rem;
    max-width: 80%;
    padding: 5rem;
}

.main-footer a
{
color: #8B0000;
text-decoration: underline;
}

/* Showcase */
.showcase
{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
    background-image: url(/Project1/static/media/DS.19e2fa23.png);
}

.showcase .showcase-text
{
font-size: 3rem;
width: 100%;
color: #fff;
margin-bottom: 1.5rem;
}

.showcase .secondary-btn
{
    width: 60%;
}


.Main2 h1{
    font-family: 'Open Sans', sans-serif;
    font-size: larger;
}


.Main{
    padding-right: 3%;
    padding-left: 3%;
}

@media (max-width: 768px){ 
    .Main {
    padding-left: 1rem;
    padding-right: 1rem;
}
}

.Main2{
    padding-top: 0.5rem;
    display: block;
    overflow: hidden;
}

.PopularAnime{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    height: 325px;
}

.PopularAnime .aj{
    padding: 0.5rem;
    flex: 0 0 200px;
}

.PopularAnime::-webkit-scrollbar{
    overflow: hidden;
}




